.container {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 75px;
  flex-wrap: wrap;
  font-size: 16px;
  align-items: center;
  font-family: 'Open Sans';
  justify-content: center;
  background-color: #1a1818;
}
.text {
  color: #fff;
  width: 100%;
  display: block;
  font-size: 65px;
  align-self: flex-end;
  text-align: center;
  font-family: Lucida Console;
  font-weight: bold;
}
.text1 {
  color: #fff;
  width: 100%;
  display: block;
  font-size: 20px;
  align-self: flex-start;
  text-align: center;
  transition-delay: 0s;
  transition-timing-function: ease-in;
}
